  border-radius: 6px; }
  @media only screen and (max-width: 960px) {
    .Container {
      margin: 0; } }

.Container a {
  text-decoration: none; }

.Container em {
  font-style: italic; }

.Resume h1 {
  text-align: center;
  text-transform: uppercase;
  font: 700 28px/1 "Montserrat", sans-serif;
  margin: 30px 0 0;
  letter-spacing: -1.5px; }

.Resume h2 {
  margin: 5px 0;
  font: 700 18px "Montserrat", sans-serif;
  color: #bbb;
  text-transform: uppercase;
  letter-spacing: -0.5px; }

.Resume h3 {
