  text-transform: uppercase;
  margin: 0px 0 4px;
  letter-spacing: -0.5px; }
  .Resume h3 + p {
    margin: 4px 0; }
  .Resume h3 small {
    font-weight: 400;
    text-transform: none;
    float: right; }
    @media only screen and (max-width: 768px) {
      .Resume h3 small {
        display: block;
        float: none; } }

.Resume a {
  color: #205364; }
  .Resume a:hover {
