.Resume p {
  margin: 0;
  padding: 0; }

.Resume section {
  padding: 20px 18px;
  border-bottom: 1px solid #ddd; }
  .Resume section:last-child {
    border-bottom: none; }

.Resume .two-columns {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #ddd; }
  @media only screen and (max-width: 800px) {
    .Resume .two-columns {
