      .Resume .two-columns section {
        border-bottom: 1px solid #ddd; } }

.Resume .two-columns .column:first-child {
  flex: 2 1;
  border-right: 1px solid #ddd; }

.Resume .two-columns .column:last-child {
  flex: 1 1; }

.Resume .timeline {
  position: relative;
  margin-top: 20px; }
  .Resume .timeline .line {
    position: absolute;
    background: #000;
    height: 100%;
    width: 1.5px;
    top: 15px;
    left: 8.5px; }
  .Resume .timeline.condensed .line {
    top: 7px; }

.Resume .timeline-item {
  padding: 0px 15px 0px 30px;
  position: relative;
  margin-bottom: 15px; }
  .Resume .timeline-item:before {
    content: "";
    position: absolute;
    background: #fff;
    border: 1.5px solid #000;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    top: 5px;
    left: 4px; }
  .Resume .timeline-item:not(:last-child):after {
    content: "";
    z-index: -1;
    position: absolute;
    border: 1.5px solid black;
    height: calc(100% + 15px);
    top: 9px;
    left: 8px; }
  .Resume .timeline-item:last-child {
    margin-bottom: 0; }
  .Resume .timeline-item ul {
    margin-bottom: 20px; }

.Resume ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }
  .Resume ul li {
    padding: 2px 0 0 20px;
    position: relative; }
    .Resume ul li:before {
      content: "\2014";
      position: absolute;
      left: 0; }

.Resume .even-columns {
  display: flex;
  margin: 0 -15px; }
  .Resume .even-columns .even-columns-child {
    flex: 1 1;
    padding: 10px 15px; }
    .Resume .even-columns .even-columns-child img.bg {
      z-index: -1;
      display: block;
      width: 100%;
      position: absolute;
      top: -5px;
      bottom: 2.5px;
      left: 0; }
    .Resume .even-columns .even-columns-child div.bg {
      z-index: -1;
      display: block;
      width: 100%;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0; }
    .Resume .even-columns .even-columns-child .translucent-bg {
      padding: 10px 5px;
      width: 100%;
      background: rgba(255, 255, 255, 0.85);
      box-sizing: border-box;
      box-shadow: 0 0 8px 2px rgba(32, 32, 97, 0.1); }
  .Resume .even-columns.center {
    text-align: center; }

.Resume .interest-icon {
  width: 70px;
  height: 70px;
  margin: 5px auto;
  border: 1.5px solid #000;
  border-radius: 50%; }
  .Resume .interest-icon + p {
    margin: 10px 0 -10px; }
  .Resume .interest-icon img {
    width: 60%;
    margin: 15px 0 0; }
  .Resume .interest-icon svg {
    height: 100%; }

.Resume .credit {
  color: #fff;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  text-align: center; }
  .Resume .credit a {
    color: #fff; }

.Resume footer {
  text-align: center;
  margin: 3em auto; }
  .Resume footer a {
    text-decoration: none;
    display: inline-block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: #111;
    color: #f8f8f8;
    margin: 5px; }
    .Resume footer a:hover {
      background: #222; }
    .Resume footer a .icons {
      margin-top: 12px;
      display: inline-block;
      font-size: 20px; }

.ProjectsComponent .even-columns {
  margin: 0 -18px; }

.ProjectsComponent .invisible {
  font-size: 0;
  line-height: 0; }

